import React, { useState } from 'react';

const CallToAction = () => {
  const [email, setEmail] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [message, setMessage] = useState({ text: '', type: '' });

  const isValidEmail = (email) => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  };

  const showMessage = (text, type) => {
    setMessage({ text, type });
    setTimeout(() => {
      setMessage({ text: '', type: '' });
    }, 4000);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!email || !isValidEmail(email)) {
      showMessage('Please enter a valid email address.', 'error');
      return;
    }

    setIsSubmitting(true);

    // Simulate API call
    try {
      await new Promise(resolve => setTimeout(resolve, 1500));
      showMessage('Thank you! You\'ve been added to our waitlist. We\'ll be in touch soon!', 'success');
      setEmail('');
    } catch (error) {
      showMessage('Something went wrong. Please try again.', 'error');
    } finally {
      setIsSubmitting(false);
    }
  };

  const socialLinks = [
    { icon: 'fab fa-linkedin', href: '#', label: 'LinkedIn' },
    { icon: 'fab fa-twitter', href: '#', label: 'Twitter' },
    { icon: 'fab fa-facebook', href: '#', label: 'Facebook' }
  ];

  const contactLinks = [
    { label: 'Careers', href: 'mailto:careers@waynova.com' },
    { label: 'Investors', href: 'mailto:investors@waynova.com' }
  ];

  return (
    <section id="cta" className="py-20 bg-gradient-to-r from-blue-600 to-purple-600 text-white">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 text-center">
        <div data-aos="fade-up">
          <h2 className="text-4xl md:text-5xl font-bold mb-6">
            Ready to Transform Your Sales Process?
          </h2>
          <p className="text-xl md:text-2xl text-blue-100 mb-12 max-w-3xl mx-auto">
            Join hundreds of sales professionals who are already accelerating their public sector success with Waynova.
          </p>
        </div>
        
        <div data-aos="fade-up" data-aos-delay="200" className="max-w-md mx-auto mb-12">
          <form onSubmit={handleSubmit} className="flex flex-col sm:flex-row gap-4">
            <input 
              type="email" 
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              placeholder="Enter your work email" 
              className="flex-1 px-6 py-4 rounded-lg text-gray-900 placeholder-gray-500 focus:outline-none focus:ring-4 focus:ring-white/30"
              required
              disabled={isSubmitting}
            />
            <button 
              type="submit" 
              disabled={isSubmitting}
              className="bg-white text-blue-600 px-8 py-4 rounded-lg font-semibold hover:bg-gray-100 transition-colors whitespace-nowrap disabled:opacity-50 disabled:cursor-not-allowed"
            >
              {isSubmitting ? 'Joining...' : 'Get Early Access'}
            </button>
          </form>
          
          {/* Success/Error Message */}
          {message.text && (
            <div className={`mt-4 p-4 rounded-lg transition-all ${
              message.type === 'success' 
                ? 'bg-green-500 text-white' 
                : 'bg-red-500 text-white'
            }`}>
              {message.text}
            </div>
          )}
          
          <p className="text-sm text-blue-100 mt-4">
            Join our waitlist for exclusive early access and beta testing opportunities.
          </p>
        </div>
        
        <div data-aos="fade-up" data-aos-delay="400" className="border-t border-white/20 pt-12">
          <div className="flex flex-col md:flex-row justify-between items-center space-y-6 md:space-y-0">
            <div>
              <h3 className="text-2xl font-bold mb-2">Waynova</h3>
              <p className="text-blue-100">AI-Powered Grant Intelligence Platform</p>
            </div>
            
            <div className="flex space-x-8">
              <div className="text-center">
                <div className="text-sm text-blue-100 mb-2">Connect</div>
                <div className="flex space-x-4">
                  {socialLinks.map((link, index) => (
                    <a 
                      key={index}
                      href={link.href} 
                      className="text-white hover:text-blue-200 transition-colors"
                      aria-label={link.label}
                    >
                      <i className={`${link.icon} text-xl`}></i>
                    </a>
                  ))}
                </div>
              </div>
              
              <div className="text-center">
                <div className="text-sm text-blue-100 mb-2">Contact</div>
                <div className="space-y-1">
                  {contactLinks.map((link, index) => (
                    <div key={index}>
                      <a 
                        href={link.href} 
                        className="text-white hover:text-blue-200 transition-colors"
                      >
                        {link.label}
                      </a>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
          
          <div className="mt-8 pt-8 border-t border-white/20 text-center">
            <p className="text-blue-100">&copy; 2024 Waynova. All rights reserved.</p>
          </div>
        </div>
      </div>
    </section>
  );
};

export default CallToAction;