import React, { useState, useEffect } from 'react';

const Testimonials = () => {
  const [currentTestimonial, setCurrentTestimonial] = useState(0);

  const testimonials = [
    {
      quote: "Waynova transformed our grant discovery process. What used to take weeks now takes hours, and our success rate has doubled. The AI matching is incredibly accurate.",
      name: "Sarah Johnson",
      title: "Sales Director, TechCorp Solutions",
      initials: "SJ",
      color: "bg-blue-500"
    },
    {
      quote: "The confidence scoring feature is a game-changer. We can now prioritize our efforts on the most promising opportunities and allocate resources more effectively.",
      name: "Michael Rodriguez",
      title: "VP Sales, GovTech Innovations",
      initials: "MR",
      color: "bg-green-500"
    },
    {
      quote: "Waynova's integration with our CRM system made adoption seamless. The team was able to start seeing results immediately without changing their workflow.",
      name: "Lisa Chen",
      title: "Sales Operations Manager, PublicTech Corp",
      initials: "LC",
      color: "bg-purple-500"
    }
  ];

  useEffect(() => {
    // Auto-rotate testimonials every 5 seconds
    const interval = setInterval(() => {
      setCurrentTestimonial(prev => (prev + 1) % testimonials.length);
    }, 5000);

    return () => clearInterval(interval);
  }, [testimonials.length]);

  useEffect(() => {
    // Handle keyboard navigation
    const handleKeyDown = (e) => {
      if (e.key === 'ArrowLeft') {
        setCurrentTestimonial(prev => 
          prev > 0 ? prev - 1 : testimonials.length - 1
        );
      } else if (e.key === 'ArrowRight') {
        setCurrentTestimonial(prev => (prev + 1) % testimonials.length);
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [testimonials.length]);

  const showTestimonial = (index) => {
    setCurrentTestimonial(index);
  };

  return (
    <section id="testimonials" className="py-20 bg-white">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="text-center mb-16">
          <h2 data-aos="fade-up" className="text-4xl font-bold text-gray-900 mb-4">
            What Our Users Say
          </h2>
          <p data-aos="fade-up" data-aos-delay="200" className="text-xl text-gray-600 max-w-3xl mx-auto">
            Hear from sales professionals who are closing more deals with Waynova
          </p>
        </div>
        
        <div className="testimonial-container relative max-w-4xl mx-auto">
          <div className="testimonial-slider">
            {testimonials.map((testimonial, index) => (
              <div
                key={index}
                className={`testimonial-slide ${
                  index === currentTestimonial ? 'active' : ''
                } ${
                  index === currentTestimonial - 1 || 
                  (currentTestimonial === 0 && index === testimonials.length - 1) 
                    ? 'prev' : ''
                }`}
                data-aos={index === 0 ? "fade-up" : ""}
              >
                <div className="bg-gray-50 rounded-3xl p-8 text-center">
                  <div className="text-6xl text-blue-200 mb-4">"</div>
                  <p className="text-xl text-gray-700 mb-8 italic leading-relaxed">
                    {testimonial.quote}
                  </p>
                  <div className="flex items-center justify-center space-x-4">
                    <div className={`w-16 h-16 ${testimonial.color} rounded-full flex items-center justify-center`}>
                      <span className="text-white font-semibold text-lg">
                        {testimonial.initials}
                      </span>
                    </div>
                    <div className="text-left">
                      <div className="font-semibold text-gray-900">{testimonial.name}</div>
                      <div className="text-gray-600">{testimonial.title}</div>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
          
          {/* Navigation Dots */}
          <div className="flex justify-center space-x-2 mt-8">
            {testimonials.map((_, index) => (
              <button
                key={index}
                onClick={() => showTestimonial(index)}
                className={`testimonial-dot w-3 h-3 rounded-full transition-all ${
                  index === currentTestimonial 
                    ? 'bg-blue-600 active' 
                    : 'bg-gray-300 hover:bg-gray-400'
                }`}
                aria-label={`Go to testimonial ${index + 1}`}
              />
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

export default Testimonials;