import React, { useEffect } from 'react';

const Hero = ({ scrollToDemo, scrollToSimulator }) => {
  useEffect(() => {
    // Parallax effect for hero background elements
    const handleScroll = () => {
      const scrolled = window.pageYOffset;
      const parallax = document.querySelectorAll('.floating-element');
      const speed = 0.5;
      
      parallax.forEach(element => {
        const y = scrolled * speed;
        element.style.transform = `translateY(${y}px)`;
      });
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  return (
    <section id="hero" className="hero-section relative min-h-screen flex items-center justify-center overflow-hidden">
      {/* Animated Background Elements */}
      <div className="absolute inset-0 overflow-hidden">
        <div className="floating-element absolute top-1/4 left-1/4 w-64 h-64 bg-blue-200/30 rounded-full blur-3xl animate-pulse"></div>
        <div className="floating-element absolute top-3/4 right-1/4 w-96 h-96 bg-purple-200/20 rounded-full blur-3xl animate-pulse" style={{animationDelay: '2s'}}></div>
        <div className="floating-element absolute bottom-1/4 left-1/3 w-48 h-48 bg-indigo-200/40 rounded-full blur-2xl animate-pulse" style={{animationDelay: '4s'}}></div>
      </div>
      
      <div className="relative z-10 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 text-center">
        <div data-aos="fade-up" data-aos-duration="800">
          <h1 className="hero-title text-4xl md:text-6xl lg:text-7xl font-bold text-gray-900 mb-6 leading-tight">
            Unlock Public Sector Sales with{' '}
            <span className="bg-gradient-to-r from-blue-600 to-purple-600 bg-clip-text text-transparent">
              AI-Powered Grant Intelligence
            </span>
          </h1>
        </div>
        
        <div data-aos="fade-up" data-aos-duration="800" data-aos-delay="200">
          <p className="hero-subtitle text-xl md:text-2xl text-gray-600 mb-8 max-w-4xl mx-auto leading-relaxed">
            Waynova helps sales teams discover eligible grants and close deals faster with intelligent matching and comprehensive insights.
          </p>
        </div>
        
        <div data-aos="fade-up" data-aos-duration="800" data-aos-delay="400" className="flex flex-col sm:flex-row gap-4 justify-center items-center">
          <button 
            onClick={scrollToDemo}
            className="bg-blue-600 hover:bg-blue-700 text-white px-8 py-4 rounded-xl text-lg font-semibold transition-all transform hover:scale-105 shadow-lg hover:shadow-xl"
          >
            <i className="fas fa-calendar-alt mr-2"></i>
            Request Demo
          </button>
          <button 
            onClick={scrollToSimulator}
            className="bg-white hover:bg-gray-50 text-blue-600 px-8 py-4 rounded-xl text-lg font-semibold transition-all transform hover:scale-105 shadow-lg hover:shadow-xl border-2 border-blue-600"
          >
            <i className="fas fa-play mr-2"></i>
            Try Grant Match Simulator
          </button>
        </div>
        
        {/* Scroll Indicator */}
        <div data-aos="fade-up" data-aos-duration="800" data-aos-delay="600" className="absolute bottom-8 left-1/2 transform -translate-x-1/2">
          <div className="animate-bounce">
            <i className="fas fa-chevron-down text-2xl text-gray-400"></i>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Hero;