import React, { useEffect, useState } from 'react';

const HowItWorks = () => {
  const [linesAnimated, setLinesAnimated] = useState(false);

  useEffect(() => {
    const observerOptions = {
      threshold: 0.3,
      rootMargin: '0px 0px -50px 0px'
    };

    const observer = new IntersectionObserver((entries) => {
      entries.forEach(entry => {
        if (entry.isIntersecting && !linesAnimated) {
          setLinesAnimated(true);
          animateProcessLines();
        }
      });
    }, observerOptions);

    const section = document.getElementById('how-it-works');
    if (section) {
      observer.observe(section);
    }

    return () => {
      if (section) {
        observer.unobserve(section);
      }
    };
  }, [linesAnimated]);

  const animateProcessLines = () => {
    const lines = document.querySelectorAll('.animate-line-grow');
    lines.forEach((line, index) => {
      setTimeout(() => {
        if (line) {
          line.style.width = '100%';
        }
      }, index * 500);
    });
  };

  const steps = [
    {
      icon: 'fas fa-upload',
      title: 'Data Input',
      description: 'Upload agency information, requirements, and project details',
      delay: 100
    },
    {
      icon: 'fas fa-cogs',
      title: 'AI Matching',
      description: 'Our engine analyzes and matches against thousands of grants',
      delay: 200
    },
    {
      icon: 'fas fa-list-alt',
      title: 'Grant Matches',
      description: 'Receive prioritized matches with confidence scores',
      delay: 300
    },
    {
      icon: 'fas fa-file-pdf',
      title: 'PDF Output',
      description: 'Get comprehensive reports ready for your sales team',
      delay: 400
    }
  ];

  return (
    <section id="how-it-works" className="py-20 bg-white">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="text-center mb-16">
          <h2 data-aos="fade-up" className="text-4xl font-bold text-gray-900 mb-4">
            How Waynova Works
          </h2>
          <p data-aos="fade-up" data-aos-delay="200" className="text-xl text-gray-600 max-w-3xl mx-auto">
            Our AI-powered platform streamlines grant discovery and matching in four simple steps
          </p>
        </div>
        
        <div className="grid md:grid-cols-4 gap-8 mb-16">
          {steps.map((step, index) => (
            <React.Fragment key={index}>
              <div data-aos="fade-up" data-aos-delay={step.delay} className="text-center group">
                <div className="w-16 h-16 bg-blue-100 rounded-full flex items-center justify-center mx-auto mb-4 group-hover:bg-blue-200 transition-colors">
                  <i className={`${step.icon} text-2xl text-blue-600`}></i>
                </div>
                <div className="w-full h-2 bg-gray-200 rounded-full mb-4 md:hidden">
                  <div className="h-2 bg-blue-600 rounded-full w-1/4"></div>
                </div>
                <h3 className="text-xl font-semibold text-gray-900 mb-2">{step.title}</h3>
                <p className="text-gray-600">{step.description}</p>
              </div>
              
              {/* Connection Line - only show between steps, not after last one */}
              {index < steps.length - 1 && (
                <div className="hidden md:block relative">
                  <div className="absolute top-8 left-0 w-full h-0.5 bg-gray-300">
                    <div 
                      className="h-full bg-blue-600 animate-line-grow" 
                      style={{
                        width: '0%',
                        animationDelay: `${1 + index * 0.5}s`,
                        animationDuration: '1s',
                        animationFillMode: 'forwards'
                      }}
                    ></div>
                  </div>
                </div>
              )}
            </React.Fragment>
          ))}
        </div>
      </div>
    </section>
  );
};

export default HowItWorks;